header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  padding: 15px 0;
  z-index: 9999;
  transition: all 0.6s ease-in-out;
}
header .upper .number-wrapper {
  margin-right: 20px;
  margin-top: 10px;
}
header .upper .ctc-wrapper {
  margin-right: 20px;
}
header .upper .enq-btn-wrapper a {
  display: inline-block;
  padding: 0px 15px;
  color: #fff;
  background-color: #6b6b6b;
  text-transform: uppercase;
  line-height: 40px;
}
header .lower .nav-links {
  text-align: right;
}
header .lower .nav-links a {
  display: inline-block;
  margin-right: 20px;
  margin-top: 10px;
  color: #4C4C4C;
  position: relative;
}
header .lower .nav-links a:last-child {
  margin-right: 0px;
}
header .lower .nav-links a.active {
  color: #003E63;
}
header .lower .nav-links .themeBtn a {
  color: #fff;
  margin-top: 0;
}
header .form-container {
  position: absolute;
  width: 300px;
  top: calc(100% + 10px);
  right: 15px;
  background-color: #efece7;
  padding: 10px;
  display: none;
}
header .form-container .frmclose {
  display: none;
  display: inline-block;
  position: absolute;
  top: 10px;
  right: 10px;
  color: #000;
  font-size: 20px;
}
header .form-container .thankyou {
  margin-bottom: 10px;
  margin-bottom: 10px;
}

header.active {
  background-color: #fff;
  z-index: 100000;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1607843137);
}

.call-icon svg {
  width: 20px;
