  font-size: 14px;
}
footer .container .footerWrapper .partB .footerNavs .cols.socials .navLinks {
  display: flex;
  gap: 20px;
}
footer .container .footerWrapper .partB .footerNavs .cols.address p {
  font-size: 14px;
}
footer .container .footerWrapper .partC {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
}
@media (max-width:640px) {
  footer .container .footerWrapper .partC {
    font-size: 14px;
    flex-direction: column;
    gap: 10px 0;
  }
}

/**************** Header Menu ***********/
/* Desktop menu */
.main-menu {
  list-style: none;
  display: flex;
  gap: 20px;
  justify-content: flex-end;
}

.main-menu li {
  position: relative;
}

.main-menu li ul {
  position: absolute;
  top: 100%;
  left: 0;
  display: none;
  list-style: none;
  background: #fff;
  padding: 10px 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.main-menu li:hover > ul {
  display: block;
}

/* Dropdown items */
.main-menu li ul li {
  padding: 8px 15px;
  white-space: nowrap;
}

.main-menu .current-menu-item > a,
.main-menu .current-menu-ancestor > a {
  color: #0073aa; /* active link color */
  font-weight: 600;
}

/* Mobile styles */
.menu-toggle {
  display: none;
  background: none;
  border: none;
  font-size: 24px;
}

@media (max-width: 768px) {
  .menu-toggle {
    display: block;
  }
  .main-nav {
    display: none;
  }
  .main-nav.open {
    display: block;
  }
  .main-menu {
    flex-direction: column;
    gap: 10px;
  }
  .main-menu li ul {
    position: static;
    box-shadow: none;
  }
}
/* ------------------------------ text animation code starts ---------------------------  */
.revealOnScroll {
  opacity: 0;
}

.animated {
  -moz-animation-duration: 1.2s;
  -o-animation-duration: 1.2s;
  -webkit-animation-duration: 1.2s;
  -ms-animation-duration: 1.2s;
  animation-duration: 1.2s;
  -moz-animation-fill-mode: both;
  -o-animation-fill-mode: both;
  -webkit-animation-fill-mode: both;
