.caseStudy{
    .caseOverview{
        position: relative;
        background-color: #EAF7FF;
        .container{
            position: relative;
            z-index: 2;
        }
        .watermark{
            position: absolute;
            top: 150px;
            left: 48%;
            transform: translateX(-50%);
            z-index: 1;
        }
        &.padding{
            padding-top: 150px;
            // padding-bottom: 150px;
            @media ($max992) {
                padding-top: 70px;
            }
        }
        .caseBox{
            .partA{
                margin-top: 25px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 0 40px;
                @media ($max992) {
                    flex-direction: column;
                    gap: 15px 0;
                }
                @media ($max640) {
                    margin-top: 5px;
                }
                .para{
                    font-weight: 400;
                    font-size: 20px;
                    line-height: 100%;
                    color: #161616;
                    padding-left: 50px;
                    @media ($max992) {
                        padding-left: 0px;
                    }
                    @media ($max640) {
                        font-size: 16px;
                    }
                }
            }
            .partB{
                .caseGridBox{
                    margin: 70px 0 50px;
                    display: grid;
                    grid-template-columns: repeat(6,1fr);
                    gap: 75px 45px;
                    @media ($max992) {
                        margin: 30px 0;
                        gap: 45px 30px;
                    }
                    @media ($max640) {
                        grid-template-columns: auto;
                        gap: 10px 0;
                    }
                    .box{
                        // &:nth-of-type(1),
                        // &:nth-of-type(2),
                        // &:nth-of-type(3) {
                        //     grid-column: span 2;
                        //     @media ($max992) {
                        //         grid-column: span 3;
                        //     }
                        // }
                        // &:nth-of-type(4),
                        // &:nth-of-type(5) {
                        //     grid-column: span 2;
                        //     @media ($max992) {
                        //         grid-column: span 3;
                        //     }
                        // }
                        display: none;
                        grid-column: span 2;
                            @media ($max992) {
                                grid-column: span 3;
                            }
                        >img{
                            border-radius: 6px;
                            padding-bottom: 20px;
                        }
                        .dates{
                            font-weight: 600;
                            font-size: 12px;
                            text-transform: capitalize;
                            color: #4C4C4C;
                            padding-bottom: 10px;
                        }
                        .abstract{
                            color: #161616;
                            font-weight: 600;
                            font-size: 20px;
                            line-height: 26px;
                            padding-bottom: 10px;
                        }
                    }
                }
            }
        }
    }
    .wantResult{
        background: radial-gradient(116.19% 210.15% at 36.27% 57.58%, #89E8CD 0%, #87E4CB 14%, #83D8C6 28%, #7BC4BE 42%, #71A8B3 56%, #6384A5 70%, #535893 84%, #40257F 98%, #3D1D7C 100%);
    }
}